<template>
  <div class="home">
    <!-- 头部 -->
    <van-row align="center">
      <van-col span="4">
        <van-image class="imgs" width="50" height="50" :src="img" />
      </van-col>
      <van-col span="16">
        <van-search v-model="ipt" shape="round" placeholder="请输入搜索关键词" />
      </van-col>
      <van-col span="4">
        <van-dropdown-menu>
          <van-dropdown-item overlay="false" v-model="value1" :options="option1" />
        </van-dropdown-menu>
      </van-col>
    </van-row>
    <!-- 轮播图==》滑动 -->
    <van-swipe class="my-swipe" :autoplay="3000">
      <van-swipe-item v-for="(image, index) in images" :key="index">
        <img v-lazy="image" />
      </van-swipe-item>
    </van-swipe>
    <!--链接 -->
    <van-grid :column-num="4" color="#07c160">
      <van-grid-item  class="link" color="#07c160" v-for="item in txt" :icon="item.icon"  :key="item.title" :text="item.title" />
    </van-grid>
    <!-- 商品列表==>宫格 -->
    <van-grid :gutter="12" :border="false" :column-num="2" class="goodslist">
      <van-grid-item v-for="item in goodslist" :key="item._id" @click="gotoDetail(item._id)">
        <van-image :src="item.img_url" />
        <h4>{{item.goods_name}}</h4>
        <p class="price">
          <del>{{item.price}}</del>
          <span>{{item.sales_price}}</span>
        </p>
      </van-grid-item>
    </van-grid>
  </div>
</template>
<script>
import Vue from "vue";
import {
  Swipe,
  SwipeItem,
  Lazyload,
  Image,
  Grid,
  GridItem,
  Col,
  Row,
  Search,
  DropdownMenu,
  DropdownItem,
  Icon
} from "vant";
Vue.use(Icon);
import { Image as VanImage } from "vant";
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(VanImage);
Vue.use(Col);
Vue.use(Row);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Lazyload);
Vue.use(Image);
Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Search);
export default {
  name: "Home",
  data() {
    return {
      //装数据的空数组==》轮播图
      recommend: [],
      goodslist: [],
      img: "/img/logo.jpg",
      value1: 0,
      value2: "a",
      ipt: "",
      txt: [
        {
          icon: "fire-o",
          title: "女士资源",
        },
        {
          icon: "like-o",
          title: "男士资源",
        },

        {
          icon: "vip-card-o",
          title: "新品上市",
        },
        {
          icon: "gem-o",
          title: "推荐热卖",
        },
        {
          icon: "shop-collect-o",
          title: "精品推荐",
        },
        {
          icon: "smile-comment-o",
          title: "市场导航",
        },
         {
          icon: "star-o",
          title: "闪电发货",
        },
        {
          icon: "label-o",
          title: "代理必读",
        },
      ],
      option1: [
        { text: "广州", value: 0 },
        { text: "深圳", value: 1 },
        { text: "北京", value: 2 },
      ],
      images: [
        "/img/banner1.jpg",
        "/img/banner2.jpg",
        "/img/banner3.jpg",
        "/img/banner4.jpg",
        "/img/banner5.jpg",
        "/img/banner6.jpg",
      ],
    };
  },
  component: {},
  methods: {
    //跳到商品商情
    gotoDetail(id) {
      this.$router.push({
        name: "Goods",
        params: {
          id,
        },
      });
    },
  },
  async created() {
    //轮播图数据
    const {
      data: { data: recommend },
    } = await this.$request.get("goods", {
      params: {
        size: 5,
        sort: "sales_qty",
        total: 0,
      },
    });
    console.log("数据", recommend);
    this.recommend = recommend;
    //列表数据
    const {
      data: { data: goodslist },
    } = await this.$request.get("goods", {
      params: {
        total: 0,
      },
    });
    this.goodslist = goodslist;
  },
};
</script>
<style lang="scss" scoped>
.van-col {
  height: 50px;
  .van-image {
    height: 50px;
    width: 80px;
    background-color: red;
    img {
      display: block;
      width: 50px !important;
      height: 50px !important;
    }
  }
}
.goodslist {
  //   img {
  //   }
  h4 {
    margin-bottom: 0;
    font-size: 14px;
    height: 50px;
  }
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
}
.van-swipe-item {
  display: flex;
  height: auto;
  img {
    margin: auto;
    width: 360px;
    height: 120px;
  }
}
.van-grid-item__content{
  width: 90px;
  height: 90px;
  border-radius: 100%;
  background-color: brown;
}
// van-grid-item__content van-grid-item__content--center van-hairline
</style>